.education {
  display: flex;
  flex-direction: column;
  gap: 0.6rem;
  flex-wrap: wrap;

  .title {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    gap: 1rem;

    hr:first-of-type {
      height: 1px;
      flex: 0 0 1rem;
    }

    hr:last-of-type {
      height: 1px;
      flex: 1 0 1rem;
    }
  }

  .educ-list {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0 5%;
  }
}
